<template>
  <div class="table">
    <div class="container">
      <div class="handle-box">
        <el-button type="primary" size="medium" @click="delAll">批量删除</el-button>
        <el-button type="primary" size="medium" @click="centerDialogVisible =true">添加歌手</el-button>
        <el-input v-model="select_word" size="medium" placeholder="        请输入歌手名" class="handle-input"></el-input>
        <el-button type="primary" size="medium" class="handle-getButton" @click="getSinger">查询</el-button>
      </div>
    </div>
    <el-table size="medium" ref="multipleTable" border style="width:100%" height="680px" :data="data" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="60" align="center"></el-table-column>
      <el-table-column label="歌手图片" width="110" align="center">
        <template slot-scope="scope">
          <div class="singer-img">
            <img :src="getUrl(scope.row.pic)" style="width: 100%"/>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="歌手名" prop="name" width="100" align="center"></el-table-column>
      <el-table-column label="性别" width="60" align="center">
        <template slot-scope="scope">
          {{changeSex(scope.row.sex)}}
        </template>
      </el-table-column>
      <el-table-column width="120" label="出生日期" align="center">
        <template slot-scope="scope">
          {{attachBirthday(scope.row.birth)}}
        </template>
      </el-table-column>
      <el-table-column label="地区" prop="location" width="110" align="center"></el-table-column>
      <el-table-column label="简介" align="center" width="400">
        <template slot-scope="scope">
          <p style="height: 90px;overflow:scroll">{{scope.row.introduction}}</p>
        </template>
      </el-table-column>
      <el-table-column label="歌曲管理" width="170" align="center">
        <template slot-scope="scope">
          <el-button size="medium" @click="songEdit(scope.row.id,scope.row.name)">歌曲管理</el-button>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="280" align="center">
        <template slot-scope="scope">
          <el-button size="medium" @click="handleEdit(scope.row)">编辑</el-button>
          <el-button size="medium" type="danger" @click="handleDelete(scope.row.id)" class="button-space">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        background layout="total,prev,pager,next"
        :current-page="currentPage"
        :page-size="pageSize"
        :total="tableData.length"
        @current-change="handleCurrentChange"
      >

      </el-pagination>
    </div>
    <el-dialog title="添加歌手" :visible.sync="centerDialogVisible" width="400px" center>
      <el-form :model="registerForm" ref="registerForm" label-width="80px">
        <el-form-item prop="name" label="歌手名" size="mini">
          <el-input v-model="registerForm.name" placeholder="歌手名"></el-input>
        </el-form-item>
        <el-form-item label="性别" size="mini">
          <el-radio-group v-model="registerForm.sex">
            <el-radio :label="0">女</el-radio>
            <el-radio :label="1">男</el-radio>
            <el-radio :label="2">组合</el-radio>
            <el-radio :label="3">不明</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item prop="birth" label="生日" size="mini">
          <el-date-picker type="date" v-model="registerForm.birth" placeholder="选择日期"
                          style="width: 100%"></el-date-picker>
        </el-form-item>
        <el-form-item prop="location" label="地区" size="mini">
          <el-input v-model="registerForm.location" placeholder="地区"></el-input>
        </el-form-item>
        <el-form-item prop="introduction" label="简介" size="mini">
          <el-input v-model="registerForm.introduction" placeholder="简介" type="textarea"></el-input>
        </el-form-item>

      </el-form>
      <span slot="footer">
        <el-button size="medium" @click="addSinger">确定</el-button>
        <el-button size="medium" @click="centerDialogVisible=false">取消</el-button>
     </span>
    </el-dialog>

    <el-dialog title="编辑歌手" :visible.sync="editVisible" width="400px" center>
      <el-form :model="editForm" ref="editForm" label-width="80px">
        <el-form-item prop="name" label="歌手名" size="mini">
          <el-input v-model="editForm.name" placeholder="歌手名"></el-input>
        </el-form-item>
        <el-form-item label="性别" size="mini">
          <el-radio-group v-model="editForm.sex">
            <el-radio :label="0">女</el-radio>
            <el-radio :label="1">男</el-radio>
            <el-radio :label="2">组合</el-radio>
            <el-radio :label="3">不明</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="生日" size="mini">
          <el-date-picker type="date" v-model="editForm.birth" placeholder="选择日期"
                          style="width: 100%"></el-date-picker>
        </el-form-item>
        <el-form-item prop="location" label="地区" size="mini">
          <el-input v-model="editForm.location" placeholder="地区"></el-input>
        </el-form-item>
        <el-form-item prop="introduction" label="简介" size="mini">
          <el-input v-model="editForm.introduction" placeholder="简介" type="textarea"></el-input>
        </el-form-item>

      </el-form>
      <span slot="footer">
        <el-button size="medium" @click="editSave">确定</el-button>
        <el-button size="medium" @click="editVisible=false">取消</el-button>
     </span>
    </el-dialog>


    <el-dialog title="删除歌手" :visible.sync="delVisible" width="400px" center>
      <div align="center">删除不可恢复，是否确定删除</div>
      <span slot="footer">
        <el-button size="medium" @click="delRow">确定</el-button>
        <el-button size="medium" @click="delVisible=false">取消</el-button>
     </span>
    </el-dialog>


  </div>
</template>

<script>
  import {setSinger, getAllSinger, updateSinger, delSinger} from '../api/index'
  import {mixin} from '../mixins'

  export default {
    name: 'SingerPage',
    mixins: [mixin],
    data () {

      return {
        centerDialogVisible: false,
        editVisible: false,
        delVisible: false,
        registerForm: {
          name: '',
          sex: '',
          birth: '',
          location: '',
          introduction: '',
        },
        editForm: {
          id: '',
          name: '',
          sex: '',
          birth: '',
          location: '',
          introduction: '',
        },

        tableData: [],
        tempData: [],
        select_word: '',
        pageSize: 5,
        currentPage: 1,
        idx: -1,
        multipleSelection: []
      }

    },
    computed: {
      data () {
        return this.tableData.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
      }
    },
    created () {
      this.getData()
    },
    methods: {
      getSinger () {
        if (this.select_word == '') {
          this.tableData = this.tempData
        } else {
          this.tableData = []
          for (let item of this.tempData) {
            if (item.name.includes(this.select_word)) {
              this.tableData.push(item)
            }
          }
        }
      },
      //获取当前页
      handleCurrentChange (val) {
        this.currentPage = val
      },
      //查询所有歌手
      getData () {
        this.tableData = []
        this.tempData = []
        getAllSinger().then(res => {
          this.tableData = res
          this.tempData = res

        })
      },
      //弹出编辑页面
      handleEdit (row) {
        this.editVisible = true
        this.editForm = {
          id: row.id,
          name: row.name,
          sex: row.sex,
          birth: row.birth,
          location: row.location,
          introduction: row.introduction,
        }
      },
      // 保存编辑页面修改的数据
      editSave () {
        let d = new Date(this.editForm.birth)
        let datetime = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
        let params = new URLSearchParams()
        params.append('id', this.editForm.id)
        params.append('name', this.editForm.name)
        params.append('sex', this.editForm.sex)
        params.append('birth', datetime)
        params.append('location', this.editForm.location)
        params.append('introduction', this.editForm.introduction)

        updateSinger(params)
          .then(res => {
            if (res.code == 1) {
              this.getData()
              this.notify('修改成功', 'success')
            } else {
              this.notify('修改失败', 'error')
            }
          })
          .catch(err => {
            console.log(err)
          })
        this.editVisible = false
      },
      //添加歌手
      addSinger () {
        let d = this.registerForm.birth
        let datetime = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
        let params = new URLSearchParams()
        params.append('name', this.registerForm.name)
        params.append('sex', this.registerForm.sex)
        params.append('pic', '/img/singerPic/hhh.jpg')
        params.append('birth', datetime)
        params.append('location', this.registerForm.location)
        params.append('introduction', this.registerForm.introduction)

        setSinger(params)
          .then(res => {
            if (res.code == 1) {
              this.getData()
              this.notify('添加成功', 'success')
            } else {
              this.notify('添加失败', 'error')
            }
          })
          .catch(err => {
            console.log(err)
          })
        this.centerDialogVisible = false
      },
      //删除一名歌手
      delRow () {
        delSinger(this.idx)
          .then(res => {
            if (res) {
              this.getData()
              this.notify('删除成功', 'success')
            } else {
              this.notify('删除失败', 'error')
            }
          })
          .catch(err => {
            console.log(err)
          })
        this.delVisible = false
      },
      //转向歌曲管理页面
      songEdit(id,name){
        this.$router.push({path:`/Song`,query:{id,name}});
      }
    }

  }
</script>

<style scoped>

  .button-space{
    margin-left: 30px;
    display: inline-block;
  }

  .handle-box {
    margin-bottom: 0;
  }

  .singer-img {
    width: 100%;
    height: 80px;
    border-radius: 5px;
    margin-bottom: 5px;
    overflow: hidden;
  }

  .handle-input {
    margin-left: 30px;
    width: 180px;
    display: inline-block;
  }

  .handle-getButton{
    margin-left: 10px;
    display: inline-block;
  }

  .pagination {
    display: flex;
    justify-content: center;
  }
</style>



